body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo-icon {
  padding: 10px;
  margin: auto;
}
.logo-icon img {
  max-width: 64px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  height: 55px;
  box-shadow: rgb(234 229 229 / 35%) 0px 5px 8px;
  background-color: #f5f2f29e;
  align-items: center;
}

.shop-title {
  font-size: 24px;
  font-weight: 700px;
  padding: 8px 24px;
  flex-grow: 1;
}

.header .search {
  padding-right: 16px;
}

.header .search input {
  width: 200px;
  border-radius: 12px;
  border: 1px solid #ffff;
  outline: none;
  padding: 5px;
  transition: width 0.7s;
}

.header .search input:focus {
  width: 350px;
}

.header .cart {
  padding-right: 12px;
  cursor: pointer;
}

/*
  Items
*/

.items-container {
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
}

.items-container .item {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 12px;
  margin: 8px;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  border-radius: 5px;
}

.items-container .item:hover {
  box-shadow: rgb(90 160 181 / 35%) 0px 5px 15px;
}

.items-container .item .item-image img {
  max-width: 300px;
}

.item-name {
  font-size: 13px;
  font-weight: 500;
  color: #867f7f;
  padding: 4px;
}

.item-price {
  font-size: 14px;
  font-weight: 700;
  color: #3a3333;
  padding: 8px;
}

.item-category {
  cursor: pointer;
  display: flex;
  flex-direction: row-reverse;
  font-size: 10px;
}

/*
Item Detail
*/
/* .item-detail {
  display: flex;
  flex-direction: column;
} */
.item-detail .item-name h1 {
  font-size: 25px;
  letter-spacing: 1px;
}

.item-detail .item-price {
  font-size: 18px;
  font-weight: 700;
  color: #5f4f4f;
  padding: 8px;
  letter-spacing: 2px;
}

.item-detail .item-image img {
  width: 100%;
  box-shadow: rgb(90 160 181 / 35%) 0px 5px 15px;
  margin: 16px 0px;
}

.item-detail .add-to-cart {
  position: fixed;
  bottom: 0;
  padding: 16px;
}

.item-detail .add-to-cart button {
  border-radius: 12px;
  background: #b3aaa5;
  color: #fff;
  border: 0;
  padding: 14px 30px;
  width: 300px;
  font-size: 15px;
  cursor: pointer;
}

.item-detail .add-to-cart button:hover {
  box-shadow: rgb(90 160 181 / 35%) 0px 5px 15px;
  color: #b37373;
}

.quantity .add-remove-btn {
  padding: 8px 24px;
}
